import { PointsService } from "app/admin/services/Admin/points.service";
import { Component, OnInit } from "@angular/core";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { ActivatedRoute } from "@angular/router";
import {
  IAddLecturesCustomePointsVM,
  ILecturePointVM,
} from "app/admin/models/admin/point";
import { NgxSpinnerService } from "ngx-spinner";
import { ToastrService } from "ngx-toastr";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";

@Component({
  selector: "app-material-custome-points",
  templateUrl: "./material-custome-points.component.html",
  styleUrls: ["./material-custome-points.component.scss"],
})
export class MaterialCustomePointsComponent implements OnInit {
  subjectId: any;
  materialId: any;
  Points: ILecturePointVM[];
  myForm: FormGroup;
  submitted = false;
  p: number = 1;
  maxSize = 9;
  functionId: number;
  itemsCount?: number;

  constructor(
    private route: ActivatedRoute,
    private fb: FormBuilder,
    public toastr: ToastrService,
    public PointsService: PointsService,
    private dialog: MatDialog,
    private spinner: NgxSpinnerService
  ) {
    this.route.queryParamMap.subscribe((params) => {
      this.subjectId = params.get("subjectId");
    });
    this.route.paramMap.subscribe((params) => {
      this.materialId = params.get("materialId");
    });
    this.route.queryParamMap.subscribe((params) => {
      this.subjectId = params.get("subjectId");
      this.route.queryParamMap.subscribe((params) => {
        this.functionId = Number(params.get("functionId"));
      });
    });
  }

  ngOnInit(): void {
    this.getLectureCustomPoints(1);

    this.myForm = this.fb.group({
      itemId: [this.materialId, Validators.required],
      ByQuestion: [false, Validators.required],
      questionAttempt: [true, Validators.required],
      value: [0, [Validators.required, CustomeValidator.minusNum]],
      questionAttempts: [0, [CustomeValidator.minusNum]],
      attendancevalue: ["", [Validators.required, CustomeValidator.minusNum]],
      absencevalue: ["", [Validators.required, CustomeValidator.minusNum]],
    });
    this.changeQuestionAttemptsValidation(true);
  }

  get FormCtrl() {
    return this.myForm.controls;
  }

  //Add Custom Material Point
  addLectureCustomPoint() {
    this.submitted = true;
    let data: IAddLecturesCustomePointsVM = {
      itemId: this.FormCtrl?.itemId.value,
      ByQuestion: this.FormCtrl?.ByQuestion.value,
      absencevalue: this.FormCtrl?.absencevalue.value,
      attendancevalue: this.FormCtrl?.attendancevalue.value,
      questionAttempts: this.FormCtrl?.questionAttempts.value,
      questionAttempt: this.FormCtrl?.questionAttempt.value,
      value: this.FormCtrl?.value.value,
    };

    if (this.myForm.valid) {
      this.spinner.show();
      this.PointsService.addLectureCustomPoint(data).subscribe((res: any) => {
        if (res.returnValue == 1) {
          this.getLectureCustomPoints(1);
          this.toastr.success("تم إاضافة النقاط");
          this.submitted = false;
          this.myForm.reset();
          this.FormCtrl?.ByQuestion.setValue(true);
          this.FormCtrl?.questionAttempt.setValue(true);
        }
        this.spinner.hide();
      });
    }
  }

  //Add Custom Material Point
  getLectureCustomPoints(p: number) {
    this.spinner.show();
    this.PointsService.getLectureCustomPoints(p, this.materialId).subscribe(
      (res: any) => {
        if (res.returnValue == 1) {
          this.itemsCount = res.itemsCount;
          this.Points = res.points;
        }
        this.spinner.hide();
      }
    );
  }

  deleteLectureCustomPoint(point: ILecturePointVM) {
    this.spinner.show();
    this.PointsService.deleteLectureCustomPoint(point.Id).subscribe(
      (res: any) => {
        if (res.returnValue == 1) {
          let i = this.Points.findIndex((e) => e === point);
          this.Points.splice(i, 1);
          this.toastr.success("تم الحذف");
        }
        this.spinner.hide();
      }
    );
  }

  DeleateLecturePointDialog(point: ILecturePointVM): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: `${point.Value}: points`,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteLectureCustomPoint(point);
      });
  }

  track(index: number, point: ILecturePointVM): number {
    return point.Id;
  }

  changeQuestionAttemptsValidation(questionAttempt: boolean) {
    if (questionAttempt == true) {
      this.myForm.controls["questionAttempts"].setValidators([
        Validators.required,
        CustomeValidator.minusNum,
      ]);
    }

    if (questionAttempt == false) {
      this.myForm.controls["questionAttempts"].reset();
      this.myForm.controls["questionAttempts"].clearValidators();
    }
    this.FormCtrl.questionAttempts.updateValueAndValidity();
  }
}
